@import url(https://fonts.googleapis.com/css?family=Noto+Sans+JP:400,700,900&subset=japanese);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.root {
  background-color: #201e2f;
  color: white;
  font-size: 14px;
}
a,
a:visited {
  color: white;
}

#copied-text {
  display: block;
  position: fixed;
  right: 500px;
  bottom: 15px;
  color: white;
  background-color: #343145;
  width: 220px;
  height: 90px;
  padding: 30px;
  font-size: 22px;
  border: 1px solid gray;
  box-sizing: border-box;
  z-index: 9999;
}

.param-text {
  padding-left: 10px;
}

.param-text2 {
  padding-left: 45px;
}

div#param-area {
  line-height: 35px;
  padding-bottom: 60px;
}

#option-text {
  padding: 2px 8px;
  margin: 0 5px;
  border: 1px solid gray;
  border-radius: 2px;
  font-family: inherit;
  font-size: 0.85em;
}

#commentary-area {
  color: #bebebe;
}

#title-area > h1 {
  display: inline-block;
  margin-top: 0;
  margin-right: 20px;
}

h3 {
  color: white;
  margin-bottom: 5px;
}

.outputBox {
  position: fixed;
  left: 5px;
  bottom: 8px;
  color: white;
  background-color: #343145;
  width: calc(100% - 500px);
  padding: 15px 15px 5px 15px;
  font-size: 16px;
  border: 1px solid gray;
  box-sizing: border-box;
  opacity: 0.8;
}

.inputBox {
  width: 96px;
  padding: 0.3em;
  transition: 0.3s;
  letter-spacing: 1px;
  color: #aaaaaa;
  border: none;
  border-bottom: 1px solid white;
  background: transparent;
}

.inputBox:focus {
  border-bottom: 2px solid #67c5ff;
  outline: none;
}

iframe {
  border: 4px gray solid;
  background-color: #343145;
  opacity: 0.8;
}

#previewFrame {
  transform: scale(0.25);
  -o-transform: scale(0.25);
  -webkit-transform: scale(0.25);
  -moz-transform: scale(0.25);
  -ms-transform: scale(0.25);
  transform-origin: 0 0;
  -o-transform-origin: 0 0;
  -webkit-transform-origin: 0 0;
  -moz-transform-origin: 0 0;
  -ms-transform-origin: 0 0;
}

.preview-text {
  position: fixed;
  bottom: 257px;
  right: 406px;
  font-weight: bold;
  text-shadow: 2px 2px 0 #000, -2px -2px 0 #000, -2px 2px 0 #000, 2px -2px 0 #000, 0px 2px 0 #000, 0-2px 0 #000,
    -2px 0 0 #000, 2px 0 0 #000;
  opacity: 0.7;
  color: deepskyblue;
  z-index: 9999;
}

.output-text {
  position: fixed;
  bottom: 27px;
  left: 15px;
  font-weight: bold;
  text-shadow: 2px 2px 0 #000, -2px -2px 0 #000, -2px 2px 0 #000, 2px -2px 0 #000, 0px 2px 0 #000, 0-2px 0 #000,
    -2px 0 0 #000, 2px 0 0 #000;
  opacity: 0.7;
  color: deepskyblue;
  z-index: 9999;
}

#iframe-area {
  position: fixed;
  width: 480px;
  height: 270px;
  bottom: 10px;
  right: 10px;
}

.chkbx {
  display: none;
}

.chkbx-parts {
  position: relative;
  padding: 3px 5px 3px 45px;
  transition: 0.3s;
  border-radius: 5px;
  display: inline-block;
  line-height: 1;
}

.chkbx:checked + .chkbx-parts {
  color: #ffa200;
}

.chkbx-parts::before {
  content: "";
  display: block;
  left: 9px;
  width: 30px;
  height: 15px;
  border-radius: 15px;
  border: 1px solid #aaa;
  background: #fff;
  position: absolute;
}

.chkbx-parts::after {
  content: "";
  display: block;
  position: absolute;
  top: 5px;
  left: 11px;
  width: 13px;
  height: 13px;
  background: #aaa;
  border-radius: 50%;
  transition: 0.2s;
  display: block;
}

.chkbx:checked + .chkbx-parts::before {
  border: 1px solid #ffa200;
}

.chkbx:checked + .chkbx-parts::after {
  left: 26px;
  background: #ffa200;
}

img.mark {
  display: none;
}

.chatArea-wrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.chatArea {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  padding-right: 17px; /* Increase/decrease this value for cross-browser compatibility */
  box-sizing: content-box; /* So the width will be 100% + 17px */
}

.chat {
  position: fixed;
  color: white;
  opacity: 0.8;
  font-family: "Noto Sans JP";
  font-weight: bold;
  white-space: nowrap;
  z-index: 4;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.43), -1px 1px 2px rgba(0, 0, 0, 0.43), 1px -1px 2px rgba(0, 0, 0, 0.43),
    -1px -1px 2px rgba(0, 0, 0, 0.43), 1px 0px 2px rgba(0, 0, 0, 0.43), 0px 1px 2px rgba(0, 0, 0, 0.43),
    -1px 0px 2px rgba(0, 0, 0, 0.43), 0px -1px 2px rgba(0, 0, 0, 0.43);
}

.comment-wrapper {
  display: inline-block;
  background-color: rgba(1, 1, 1, 0.43);
  padding: 0 10px 0 10px;
  margin: 2px 0 2px 0;
  border-radius: 14px;

  color: white;
  font-family: "Noto Sans JP";
  font-weight: bold !important;
  text-shadow: #000 1px 1px 0, #000 -1px 1px 0, #000 1px -1px 0, #000 -1px -1px 0;
}

.versionArea {
  position: fixed;
  right: 10px;
  bottom: 5px;
  opacity: 0.8;
  font-size: 12px;
}

.iconSize {
  height: 60px;
}

.gift {
  opacity: 0.8;
  position: fixed;
}

.notificationArea {
  position: fixed;
  font-family: "Noto Sans JP";
  font-weight: bold;
  display: none;
  white-space: nowrap;
  position: fixed;
  bottom: -10px;
  color: white;
  font-size: 60px;
  z-index: 4;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.43), -1px 1px 2px rgba(0, 0, 0, 0.43), 1px -1px 2px rgba(0, 0, 0, 0.43),
    -1px -1px 2px rgba(0, 0, 0, 0.43), 1px 0px 2px rgba(0, 0, 0, 0.43), 0px 1px 2px rgba(0, 0, 0, 0.43),
    -1px 0px 2px rgba(0, 0, 0, 0.43), 0px -1px 2px rgba(0, 0, 0, 0.43);
}

