@import url("https://fonts.googleapis.com/css?family=Noto+Sans+JP:400,700,900&subset=japanese");

img.mark {
  display: none;
}

.chatArea-wrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.chatArea {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  padding-right: 17px; /* Increase/decrease this value for cross-browser compatibility */
  box-sizing: content-box; /* So the width will be 100% + 17px */
}

.chat {
  position: fixed;
  color: white;
  opacity: 0.8;
  font-family: "Noto Sans JP";
  font-weight: bold;
  white-space: nowrap;
  z-index: 4;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.43), -1px 1px 2px rgba(0, 0, 0, 0.43), 1px -1px 2px rgba(0, 0, 0, 0.43),
    -1px -1px 2px rgba(0, 0, 0, 0.43), 1px 0px 2px rgba(0, 0, 0, 0.43), 0px 1px 2px rgba(0, 0, 0, 0.43),
    -1px 0px 2px rgba(0, 0, 0, 0.43), 0px -1px 2px rgba(0, 0, 0, 0.43);
}

.comment-wrapper {
  display: inline-block;
  background-color: rgba(1, 1, 1, 0.43);
  padding: 0 10px 0 10px;
  margin: 2px 0 2px 0;
  border-radius: 14px;

  color: white;
  font-family: "Noto Sans JP";
  font-weight: bold !important;
  text-shadow: #000 1px 1px 0, #000 -1px 1px 0, #000 1px -1px 0, #000 -1px -1px 0;
}

.versionArea {
  position: fixed;
  right: 10px;
  bottom: 5px;
  opacity: 0.8;
  font-size: 12px;
}

.iconSize {
  height: 60px;
}

.gift {
  opacity: 0.8;
  position: fixed;
}

.notificationArea {
  position: fixed;
  font-family: "Noto Sans JP";
  font-weight: bold;
  display: none;
  white-space: nowrap;
  position: fixed;
  bottom: -10px;
  color: white;
  font-size: 60px;
  z-index: 4;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.43), -1px 1px 2px rgba(0, 0, 0, 0.43), 1px -1px 2px rgba(0, 0, 0, 0.43),
    -1px -1px 2px rgba(0, 0, 0, 0.43), 1px 0px 2px rgba(0, 0, 0, 0.43), 0px 1px 2px rgba(0, 0, 0, 0.43),
    -1px 0px 2px rgba(0, 0, 0, 0.43), 0px -1px 2px rgba(0, 0, 0, 0.43);
}
